import type { Metadata } from 'next';

export const metadata: Metadata = {
  title: 'Contact Us | EchoPilotAI',
  description: 'Get in touch with EchoPilotAI support team',
};

export default function ContactPage() {
  return (
    <div className="min-h-screen bg-gradient-to-b from-gray-900 via-gray-800 to-gray-900 text-white py-24">
      <div className="container mx-auto px-4 max-w-2xl">
        <h1 className="text-4xl font-bold mb-4 text-center bg-gradient-to-r from-blue-400 to-purple-400 bg-clip-text text-transparent">
          Contact Us
        </h1>
        <p className="text-gray-400 text-center mb-12">
          Have questions? We'd love to hear from you.
        </p>

        <div className="bg-gray-800/50 backdrop-blur-sm border border-gray-700 rounded-2xl p-8">
          <form id="contactForm" className="space-y-6">
            <div>
              <label htmlFor="name" className="block text-sm font-medium text-gray-300 mb-2">
                Name
              </label>
              <input
                type="text"
                id="name"
                name="name"
                required
                className="w-full px-4 py-3 bg-gray-900/50 border border-gray-700 rounded-lg focus:outline-none focus:border-blue-500 transition-colors"
                placeholder="Your name"
              />
            </div>

            <div>
              <label htmlFor="email" className="block text-sm font-medium text-gray-300 mb-2">
                Email
              </label>
              <input
                type="email"
                id="email"
                name="email"
                required
                className="w-full px-4 py-3 bg-gray-900/50 border border-gray-700 rounded-lg focus:outline-none focus:border-blue-500 transition-colors"
                placeholder="your@email.com"
              />
            </div>

            <div>
              <label htmlFor="message" className="block text-sm font-medium text-gray-300 mb-2">
                Message
              </label>
              <textarea
                id="message"
                name="message"
                required
                rows={6}
                className="w-full px-4 py-3 bg-gray-900/50 border border-gray-700 rounded-lg focus:outline-none focus:border-blue-500 transition-colors resize-none"
                placeholder="How can we help you?"
              />
            </div>

            <div id="formStatus" className="hidden text-center py-2 rounded-lg"></div>

            <button
              type="submit"
              className="w-full py-3 px-6 bg-gradient-to-r from-blue-500 to-purple-500 hover:from-blue-600 hover:to-purple-600 rounded-lg font-semibold transition-all duration-200 transform hover:scale-[1.02]"
            >
              Send Message
            </button>
          </form>
        </div>

        <script dangerouslySetInnerHTML={{ __html: `
          document.getElementById('contactForm')?.addEventListener('submit', async (e) => {
            e.preventDefault();
            const form = e.target;
            const formData = new FormData(form);
            const data = Object.fromEntries(formData.entries());
            const status = document.getElementById('formStatus');
            const button = form.querySelector('button[type="submit"]');

            button.disabled = true;
            button.textContent = 'Sending...';

            try {
              const response = await fetch('/api/contact', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify(data)
              });

              const result = await response.json();

              if (response.ok) {
                status.className = 'block text-center py-2 rounded-lg bg-green-500/20 text-green-400 border border-green-500/50';
                status.textContent = result.message || 'Message sent successfully!';
                form.reset();
              } else {
                throw new Error(result.error || 'Failed to send message');
              }
            } catch (error) {
              status.className = 'block text-center py-2 rounded-lg bg-red-500/20 text-red-400 border border-red-500/50';
              status.textContent = error.message || 'Something went wrong. Please try again.';
            } finally {
              button.disabled = false;
              button.textContent = 'Send Message';
            }
          });
        ` }} />
      </div>
    </div>
  );
}
